@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
/*SEARCH BAR*/
.search-bar {
    padding: 20px;
    margin: 20px 0;
    color: #ffffff;
}

.search-bar label {
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 1px;
    padding: 0 10px;
}

.search-bar input {
    color: #ffffff;
    padding: 5px;
    font-size: 17px;
    letter-spacing: 1px;
    border: none;
    border-bottom: 2px solid #ffffff;
}

/*SEARCH BAR END*/

/*RESPONSIVE*/
@media only screen and (max-width: 660px) {
    /*SEARCH BAR END*/
    .search-bar {
        margin: 0 auto;
        text-align: center;
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        z-index: 10;
    }

    .search-bar input {
        margin-top: 10px;
        width: 90%;
    }

    .search-bar input:-ms-input-placeholder {
        font-size: 13px;
    }

    .search-bar input:placeholder-shown {
        font-size: 13px;
    }
    /*SEARCH BAR END*/
}
/*RESPONSIVE*/

/*FONT IMPORTS*/

/*MOVIE CARD*/
.movie-card {
    position: relative;
    justify-content: center;
    display: grid;
    margin: 25px;
    font-size: 14px;
    padding: 10px 20px;
}

.link-style {
    text-decoration: none;
    color: #ffffff;
}

.movie-card img {
    width: 200px;
    height: 305px;
    margin-bottom: 15px;
    background-color: #000000;
}

.movie-card h3 {
    font-family: "Josefin Sans", sans-serif;
    font-size: 18px;
    letter-spacing: 1px;
    margin-bottom: 5px;
    padding-top: 10px;
}

.movie-img-wrapper {
    position: relative;
}

.movie-img-wrapper h4 {
    background-color: rgb(248, 185, 67);
    position: absolute;
    width: 40px;
    top: 93%;
    left: 85%;
    padding: 10px;
    text-align: center;
    border-radius: 50px;
}

/*MOVIE CARD END*/

/*MOVIE LIST*/
.list-container {
    padding: 20px 100px;
}

.loader-style {
    text-align: center;
    padding: 100px 0;
}

.list-container h2 {
    color: white;
}
/*MOVIE LIST*/

/*RESPONSIVE*/
@media only screen and (max-width: 660px) {
    /*MOVIE LIST*/
    .list-container {
        padding: 0 50px;
    }

    .list-container:nth-child(1) {
        padding-top: 100px;
    }

    .list-container h2 {
        text-align: center;
    }
    /*MOVIE LIST*/
}
/*RESPONSIVE*/

/*FONT INPORTS*/

/*BASE STYLING*/
html,
body {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    background-color: #31414e;
    font-family: "Montserrat", sans-serif;
}

*:focus {
    outline: 0;
    outline: none;
}

/*BASE STYLING END*/

/*ACTOR CARD*/
.actor-card {
    background-color: #000000;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    border-radius: 10px;
}

.actor-card img {
    width: 100%;
    vertical-align: middle;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.actor-info {
    background-color: #000000;
    margin: 10px 0 20px 0;
    color: white;
    border-radius: 10px;
}

.actor-info h5 {
    background-color: #000000;
    letter-spacing: 1px;
    font-size: 16px;
    font-weight: 100;
    padding: 10px 0 0 0;
}

.actor-info h4 {
    background-color: #000000;
    padding: 20px 0;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}
/*ACTOR CARD END*/

/*ACTORS LIST*/

.actors-list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 20px;
    gap: 20px;
}

/*ACTORS LIST END*/

/*RESPONSIVE DESIGN*/

@media only screen and (max-width: 1024px) {
    /*ACTORS LIST*/
    .actors-list {
        grid-gap: 40px;
        gap: 40px;
        grid-template-columns: repeat(3, 1fr);
    }
    /*ACTORS LIST END*/
}

@media only screen and (max-width: 700px) {
    /*ACTORS LIST*/
    .actors-list {
        grid-template-columns: 200px;
        justify-content: center;
    }
    /*ACTORS LIST END*/
}

/*RESPONSIVE DESIGN END*/

/*PAGE AND MOVIE DETAIL*/
.movie-page {
    display: grid;
    justify-content: center;
    text-align: center;
    padding: 5%;
}

.arrow-back {
    color: white;
    font-size: 30px;
    position: absolute;
    top: 20px;
    left: 50px;
    transition: all 0.3s;
}

.arrow-back:hover {
    cursor: pointer;
    transform: translateX(-5px);
}

.movie-details {
    display: grid;
    grid-template-columns: auto 1fr;
    margin-bottom: 50px;
    margin-left: 7em;
    margin-right: 7em;
    background-color: #000000;
    color: #ffffff;
    border-radius: 10px;
    box-shadow: 0px 20px 30px 3px rgba(0, 0, 0, 0.55);
}

.movie-page-poster img {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    width: 300px;
    text-align: center;
    vertical-align: middle;
}

.movie-numbers {
    display: flex;
    padding: 5px 0 10px;
}

.movie-numbers h4 {
    padding: 0 10px;
    font-size: 16px;
}

.movie-detail-text {
    display: flex;
    flex-direction: column;
    padding: 20px;
    text-align: left;
    border-radius: 10px;
}

.movie-detail-text,
.movie-detail-text * {
    background-color: #000000;
}

.movie-detail-text h1 {
    padding: 10px;
    font-size: 40px;
    letter-spacing: 1px;
    font-weight: 400;
    text-transform: uppercase;
}

.movie-detail-text p {
    padding-left: 10px;
    line-height: 25px;
}
/*PAGE AND MOVIE DETAIL END*/

/*TRAILER*/

.movie-trailer iframe {
    width: 50%;
    height: 300px;
    border: 5px solid #ffffff;
    border-radius: 10px;
    margin: 20px 0 70px 0;
    box-shadow: 0px 20px 30px 3px rgba(0, 0, 0, 0.55);
}

/*TRAILER END*/

/*RESPONSIVE DESIGN*/
@media only screen and (max-width: 1024px) {
    /*PAGE AND MOVIE DETAIL*/
    .movie-numbers {
        display: grid;
    }

    .arrow-back {
        font-size: 30px;
        top: 20px;
        left: 20px;
    }

    .movie-numbers h4 {
        padding: 5px;
        font-size: 16px;
    }

    .movie-details {
        grid-template-columns: 1fr;
        margin-bottom: 50px;
        margin-left: 3em;
        margin-right: 3em;
        border-radius: 0;
        box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.55);
    }

    .movie-page-poster img {
        border-radius: 10px;
        margin-bottom: 60px;
        width: 300px;
        box-shadow: 0px 20px 30px 3px rgba(0, 0, 0, 0.55);
    }

    .movie-detail-text {
        display: flex;
        box-shadow: 0px 20px 30px 3px rgba(0, 0, 0, 0.55);
    }

    .movie-detail-text h1 {
        padding: 5px;
        font-size: 30px;
    }

    .movie-detail-text p {
        line-height: 22px;
    }
    /*PAGE AND MOVIE DETAIL END*/

    /*TRAILER*/
    .movie-trailer iframe {
        width: 70%;
    }
    /*TRAILER END*/
}

@media only screen and (max-width: 700px) {
    /*PAGE AND MOVIE DETAIL*/
    .movie-details {
        margin-left: 1em;
        margin-right: 1em;
    }

    .arrow-back {
        font-size: 30px;
        top: 10px;
        left: 10px;
    }
    /*PAGE AND MOVIE DETAIL END*/

    /*TRAILER*/
    .movie-trailer iframe {
        width: 90%;
    }
    /*TRAILER END*/
}

/*RESPONSIVE DESIGN END*/

